/* ============================ */
/* Styles used throughout page */
/* ============================ */
:root {
  --forestgreen: #4DAB74;
  --seagreen: #57BE97;
  --mintgreen: #AFF1B9;
  --yellowish: #F2F3B7;
  --lmintgreen: #DCF4D4;
  --almostwhite:#D5EAE2;
  --white: #ffff;
  --black: #000000;
  --darkgrey: #202020;
}

* {
  box-sizing: border-box;
  font-family: 'Karla';
  color: rgba(0, 0, 0, 0.84);
}

body {
  margin: 0;
  font-family: 'Karla';
  font-size: 0.9vw;
}

/*font sizes*/
/*Header Size (projects, our community,...) */
h1{
  font-size: 8vh;
}

h2 {
  font-size: 4vh;
}

h3 {
  font-size: 2vh;
}

p {
  font-size: 2vh;
}

strong {
  color: rgba(0, 0, 0, 0.84);
}

/* Style the content */
.content {
  text-align: left;
  line-height: 3.5vh;
}

.puzzle {
  position: absolute;
  opacity: 0.5;
}

/* ============================ */
/* NAVBAR */
/* ============================ */

.toggle {
  position: absolute;
  top: .75 rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 25px;
  height: 12px;
  z-index: 2;
  
}
.toggle .bar {
  height: 2px;
  width: 80%;
  background-color: #4DAB74;
  background-color: var(--forestgreen);
  border-radius: 10px;
}

.topnav #name-mobile {
  font-size: 150%;
}
.topnav {
  overflow: hidden;
  background-color: #DCF4D4;
  background-color: var(--lmintgreen);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/* Style for logo items */
.topnav #nav-logo {
  display: flex;
  flex-direction: row;
  padding: 1vh 2vw;
  text-decoration: none;
}

.topnav #logo {
  width: 3vw;
  height: 3vw;
}

.topnav #name {
  margin-left: 24px;
  font-size: 150%;
  display: flex;
  align-items: center;
}

/* Styles for nav-items */
.topnav #nav-items{
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1vh 2vw;
}

/* Style the topnav links */
.topnav .not-apply, .button {
  padding: 1vh 2vw;
  text-decoration: none;
}

/* nav-items hover effects */
.topnav .not-apply:after {
  content: '';
  width: 0;
  height: 2px;
  display: block;
  margin-top: -2px;
  right: 0;
  background: rgba(0, 0, 0, 0.84);
  transition: width .3s ease;
  opacity: 0.75;
}

.topnav .not-apply:hover:after {
  width: 100%;
  left: 0;
  background: rgba(0, 0, 0, 0.84);
  opacity: 0.75;
}

/* Button styles */
.button {
  color: white;
  background-color: #4DAB74;
  background-color: var(--forestgreen);
  transition: 0.2s;
  border-radius: 12px;
}

/* Button hover effects */
.button:hover {
  background-color: #408D60;
}

