}

/* ============================ */
/* Styles used throughout page */
/* ============================ */
.home-button, .white-button {
    padding: 1.5vh 1vw;
    text-decoration: none;
    margin: 6vh 0.5vw;
    border-radius: 50px;
    transition: 0.2s;
}

.home-button {
    color: white;
    background: #6FCF97;
}

.home-button:hover {
    background-color: #4DAB74;
    text-decoration: none;
    color: white;
}

.white-button {
    color: #6FCF97;
    background: #FFFFFF;
    border: 1px solid #6FCF97;
    box-sizing: border-box;
}

.white-button:hover {
    background-color: rgb(240, 240, 240);
    text-decoration: none;
    color: #4DAB74;
}

.section {
    display: flex;
