@-webkit-keyframes float {
    to {
        transform: translate(8px, 6px);
    }
}



@keyframes float {
    to {
        transform: translate(8px, 6px);
    }
}

@-webkit-keyframes float2 {
    to {
        transform: translate(-8px, 6px);
    }
}

@keyframes float2 {
    to {
        transform: translate(-8px, 6px);
    }
}

@-webkit-keyframes float3 {
    to {
        transform: translate(8px, -6px);
    }
}

@keyframes float3 {
    to {
        transform: translate(8px, -6px);
    }
}

.hand {
    -webkit-animation: float 1.5s infinite alternate;
